<template>
  <div class="fillout-body">
    <div class="fillout-box">
      <p>入驻信息填写</p>
      <div>
        <p>
          <span>*营业执照：</span>
          <Upload @onChange="onBusiness" :scid="business" :limit="2" />
        </p>
      </div>
      <div>
        <p>
          <span>*商品质量证明：</span>
          <Upload @onChange="onMaterial" :scid="material" :limit="2" />
        </p>
      </div>
      <p>
        *商标类型选择 ：
        <input v-model="classId" type="radio" name="sex" value="0" /> 自有品牌
        <input v-model="classId" type="radio" name="sex" value="1" />共建品牌
        <input
          v-model="classId"
          type="radio"
          name="sex"
          value="2"
        />加盟蓝天品牌
      </p>
      <div>
        <p>
          <span>商标上传︰</span
          ><Upload @onChange="onBrand" :scid="brand" :limit="2" />
        </p>
      </div>
      <button @click="onclickNext">下一步</button>
    </div>
  </div>
</template>

<script>
import Upload from "@/components/upload.vue";
export default {
  components: { Upload },
  data() {
    return {
      classId: "0",
      business: [],
      material: [],
      brand: [],
    };
  },
  mounted() {},
  methods: {
    onclickNext() {
      console.log(this.business);
      if (this.business.length > 0 && this.material.length > 0) {
        this.$router.push({
          path: "/filout",
          query: {
            str: JSON.stringify(this.classId),
          },
        });
      } else {
        this.$message("营业执照或商品质量证明为空");
      }
    },
    async onBusiness(fileList) {
      console.log(fileList);
      let from = new FormData();
      from.append("file", fileList);
      from.append("merId", sessionStorage.getItem("id"));
      from.append("picType", 0);
      let { data } = await this.$http.meruoload(from);
      console.log(data);
      if (data.code == "0000") {
        this.business.push(data.data.merPicInfo.id);
      }
    },
    async onMaterial(fileList) {
      console.log(fileList);
      let from = new FormData();
      from.append("file", fileList);
      from.append("merId", sessionStorage.getItem("id"));
      from.append("picType", 1);
      let { data } = await this.$http.meruoload(from);
      console.log(data);
      if (data.code == "0000") {
        this.material.push(data.data.merPicInfo.id);
        console.log(this.material);
      }
    },
    async onBrand(fileList) {
      console.log(fileList);
      let from = new FormData();
      from.append("file", fileList);
      from.append("merId", sessionStorage.getItem("id"));
      from.append("picType", 7);
      let { data } = await this.$http.meruoload(from);
      console.log(data);
      if (data.code == "0000") {
        this.brand.push(data.data.merPicInfo.id);
        console.log(this.brand);
      }
    },
  },
};
</script>

<style scoped>
.fillout-body {
  width: 100vw;
  height: 100vh;
  background-image: url("/bg.jpg");
  display: flex;
  align-items: center;
}
.fillout-box {
  width: 700px;
  margin: 0 auto;
  padding: 10px;
  background: #fff;
  font-size: 18px;
}

.fillout-box button {
  width: 200px;
  height: 40px;
  margin: 50px 50px 0px 250px;
}
.fillout-box div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fillout-box > :nth-child(2) p {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
.fillout-box > :nth-child(2) p > :nth-child(2) {
  margin-left: 35px;
}
.fillout-box > :nth-child(5) p > :nth-child(2) {
  margin-left: 43px;
}
.fillout-box > :nth-child(3) p {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
.fillout-box > :nth-child(5) p {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
</style>